import axios from 'axios';

//const apiUrl = process.env.VUE_APP_API_URL || 'http://192.168.1.9:8000';
//const apiUrl = 'https://room.proben-ohne-krach.de:443';
//const apiUrl = 'http://192.168.2.111:8000';

const apiUrl = process.env.POK_API_URL

export const apiClient = axios.create({
  baseURL: apiUrl,
  headers: {
    'Content-Type': 'application/json',
  },
});

export const recClient = axios.create({
  baseURL: process.env.REC_API_URL,
  headers: {
    'Content-Type': 'application/json',
  },
});

// General room services
export const getRoomPresetCollections = () => apiClient.get('/room/presetcollections');
export const getCurrentRoomPresetCollection = () => apiClient.get('/room/presetcollections/current');
export const selectCurrentRoomPresetCollection = (id) => apiClient.put('/room/presetcollections/current', null, { params: { id: id } } );
export const resetRoom = () => apiClient.post('/room/reset');
export const getWifiQrImage = () => apiClient.get('/room/wifi-qr-code', { responseType: 'blob' });
export const getControlQrImage = () => apiClient.get('/room/control-qr-code', { responseType: 'blob' });
export const getControlQrImage2 = () => apiClient.get('/room/control-qr-code2', { responseType: 'blob' });
export const getRoomStatus = () => apiClient.get('/room');

// Instrument services
export const getInstruments = () => apiClient.get('/instruments');
export const getInstrumentById = (id) => apiClient.get(`/instruments/${id}`);
export const updateInstrumentVolume = (id, volume) => apiClient.patch(`/instruments/${id}/volume`, null, { params: { volume: volume }  });
export const updateInstrumentPanorama = (id, panorama) => apiClient.patch(`/instruments/${id}/panorama`, null, { params: { panorama: panorama }  });
export const selectInstrumentPreset = (id, preset_id) => apiClient.patch(`/instruments/${id}/preset`, null,  { params: { preset_id: preset_id } } );
export const toggleInstrumentMute = (id) => apiClient.patch(`/instruments/${id}/mute`);

// Tuner services
export const getTunerState = (id) => apiClient.get(`/instruments/${id}/tuner`);
export const turnTunerOn = (id) => apiClient.post(`/instruments/${id}/tuner/on`);
export const turnTunerOff = (id) => apiClient.post(`/instruments/${id}/tuner/off`);
export const getTuning = (id) => apiClient.get(`/instruments/${id}/tuning`);

// Recorder services
export const api_startRecording = (name, start_time) => recClient.post('/recorder/start', null, { params: { name: name, start_time: start_time } });
export const api_stopRecording = () => recClient.post('/recorder/stop');
export const api_playRecording = (id) => recClient.post(`/recorder/play/${id}`);
export const api_deleteRecording = (id) => recClient.delete(`/recorder/delete/${id}`);
export const api_getRecordings = (id) => recClient.get('/recorder/list-recordings');
export const api_getRecorderStatus = () => recClient.get('/recorder/status');
export const api_stopPlayback = () => recClient.post('/recorder/stop-playback');