<template>
  <q-page padding>
    <q-card flat class="q-pa-md">
      <q-card-section class="q-mt-md">
        <q-carousel
      v-model="slide"
      transition-prev="slide-right"
      transition-next="slide-left"
      swipeable
      navigation
      navigation-position="top"
      navigation-icon="radio_button_unchecked"
      animated
      height="300px"
      control-color="primary"
      class="rounded-borders bg-dark"
    >
      <q-carousel-slide v-if='isBackendAvailable' name="connect" class="column no-wrap flex-center">
        <q-icon name="wifi" color="primary" size="56px" />
        <div class="q-mt-md q-mb-md text-center">
             Du bist erfolgreich mit dem Proberaum verbunden und kannst jetzt starten.
        </div>
          <q-btn no-caps color="primary" size='lg' label="Wähle dein Instrument" @click="selectInstrument"/>
      </q-carousel-slide>
      <q-carousel-slide name="book" class="column no-wrap flex-center">
        <q-icon name="calendar_month" color="primary" size="56px" />
        <div class="q-mt-md q-mb-md text-center">
            Nutze unsere Herbstaktion mit 20% Rabatt auf alle Proberaumbuchungen! Gilt im Zeitraum vom 1.9.2024 bis 31.12.2024.

             <!-- Du willst in Studioqualität üben, mit deiner Band proben, eine
             Aufnahme erstellen oder einfach nur eine tolle Zeit verbringen?
             -->
             </div> <q-btn no-caps color="primary" size='md' label="Proberaum
             buchen" to="/booking"/> </q-carousel-slide> <q-carousel-slide
             name="news" class="column no-wrap flex-center"> <q-icon
             name="highlight" color="primary" size="56px" /> <div class="q-mt-md
             q-mb-md text-center"> Live Session - Take a seat!
             
             Einmal im Monat hast du die Möglichkeit, an unserer inspirierenden Live-Session teilzunehmen.
        </div>
          <q-btn no-caps color="primary" size='md' label="Mehr Infos & Anmeldung" @click="openLiveSessionPage"/>
      </q-carousel-slide>
    </q-carousel>

      </q-card-section>

      <q-page-sticky position="bottom-right" :offset="[28, 58]">
        <q-fab color="primary" icon="add" direction="up" vertical-actions-align="right">
          <q-fab-action color='secondary' @click="openWhatsappMessage" icon="sms" label="WhatsApp schreiben"/>
          <q-fab-action color='secondary' to="/booking" icon="calendar_month" label="Proberaum buchen"/>
          <q-fab-action color='secondary' @click="makeCall" icon="call" label="Anrufen"/>
        </q-fab>

      </q-page-sticky>
    </q-card>
  </q-page>

</template>

<script>
import { ref } from 'vue'
import { useInstrumentStore } from '@/stores/instruments-store';
import { useQuasar } from 'quasar';
import { useRouter } from 'vue-router';

export default {
  setup () {
    const CC_PHONE_NUMBER = '+4915566023148';

    const $q = useQuasar()
    const router = useRouter();

    const toggleFullscreen = () => {
      $q.fullscreen.request()
        .then(() => {
          console.log('Fullscreen mode activated')
        })
        .catch(err => {
          console.error('Failed to activate fullscreen mode:', err)
        })
    };

    const selectInstrument = () => {
      router.push('/select-instrument');
    }

    const store = useInstrumentStore();

    if (!store.isBackendAvailable) {
      store.checkBackendAvailability();
    }

    const slide = ref('book');
    if (store.isBackendAvailable /* && store.selectedInstrumentId === null*/) {
      slide.value = 'connect';
    }

    return {
      showDialog: ref(false),
      toggleFullscreen,
      slide,
      selectInstrument,
      CC_PHONE_NUMBER,
    }
  },
  data() {
    return {
    }
  },
  mounted() {
    this.checkConnectionStatus();
    
    const store = useInstrumentStore();
    if (!store.isBackendAvailable) {
      setTimeout(() => {
          this.checkConnectionStatus();
      }, 400);
    }
  },
  methods: {
    checkConnectionStatus() {
      const store = useInstrumentStore();

      if (store.isBackendAvailable && store.selectedInstrumentId === null) { 
       this.slide = 'connect';
      }
    },
    openHomePage() {
      window.open('http://www.proben-ohne-krach.de/kontakt', '_blank');
    },
    openLiveSessionPage() {
      window.open('https://www.proben-ohne-krach.de/live-session-take-a-seat-/', '_blank');
    },
    makeCall() {
      window.location.href = `tel:${this.CC_PHONE_NUMBER}`;
    },
    openWhatsappMessage() {
      const url = `https://wa.me/${this.CC_PHONE_NUMBER}`;
      
      window.open(url, '_blank');
    }
  },
  computed: {
    isBackendAvailable() {
      const store = useInstrumentStore();
      return store.isBackendAvailable;
    }
  }
}
</script>